img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  font-family: "Bloger-Sans", system-ui, sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
}
.page__body {
  min-height: 100%;
  margin: 0;
  color: #444444;
}
.page-header {
  position: relative;
  display: flex;
  flex-direction: row;
  background: #161c35;
  color: #192144;
  padding-top: 18px;
  padding-left: 26px;
  padding-right: 25px;
  padding-bottom: 20px;
  z-index: 8;
}
@media (min-width: 768px) {
  .page-header {
    background: #182044;
    justify-content: space-between;
    padding: 40px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    justify-content: stretch;
    padding: 46px 60px;
    background-color: #161c35;
  }
}
.page-header--bg {
  background-color: #182044;
}
@media (min-width: 1440px) {
  .page-header--bg {
    background-color: #182044;
  }
}
.page-header__container {
  position: relative;
  margin: 0 auto;
  width: 270px;
}
@media (min-width: 768px) {
  .page-header__container {
    width: 688px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .page-header__container {
    width: 1320px;
  }
}
.page-header__logo {
  height: 15px;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 166px;
    height: 42px;
    padding-top: 5px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo {
    width: 200px;
    height: 50px;
    padding-top: 1px;
  }
}
.page-header__image {
  width: 96px;
  height: 15px;
}
@media (min-width: 768px) {
  .page-header__image {
    width: 166px;
    height: 42px;
  }
}
@media (min-width: 1440px) {
  .page-header__image {
    width: 200px;
    height: 50px;
  }
}
.page-header__toggle {
  position: absolute;
  top: 50%;
  right: 1px;
  margin-top: -6px;
  z-index: 2;
  display: block;
  width: 25px;
  height: 11px;
  cursor: pointer;
  background-color: initial;
  border: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    right: 50%;
    margin-right: -13px;
    margin-top: -4.5px;
  }
}
@media (min-width: 1440px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__toggle::before,
.page-header__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 25px;
  height: 3px;
  border-radius: 1.5px;
  background-color: #ffffff;
}
.page-header__toggle::after {
  top: 8px;
}
.main-nav {
  display: none;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: 975px;
    margin-left: 145px;
    position: relative;
  }
  .main-nav::before {
    content: "";
    position: absolute;
    width: 60px;
    height: 2px;
    background-color: #ffffff;
    top: 28px;
    left: -105px;
  }
}
@media (min-width: 1440px) {
  .main-nav--bg::before {
    width: 44px;
    background-color: #192144;
    top: 27px;
    left: -89px;
  }
}
.main-menu {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0 25px;
  min-height: 568px;
  background-color: #ffffff;
  z-index: 8;
}
.main-menu--closed {
  display: none;
}
.main-menu--opened {
  display: block;
}
@media (min-width: 768px) {
  .main-menu {
    padding: 40px;
    min-height: 910px;
  }
}
@media (min-width: 1440px) {
  .main-menu {
    display: none;
  }
}
.main-menu__auth {
  display: none;
}
@media (min-width: 768px) {
  .main-menu__auth {
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    font-family: inherit;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.03em;
    font-weight: 400;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 30px;
    padding-right: 32px;
    color: #161C35;
    text-align: center;
    background-color: #FFD74B;
    border-radius: 25px;
    cursor: pointer;
  }
  .main-menu__auth:hover {
    background-color: #ffbd30;
  }
  .main-menu__auth:active {
    color: rgba(22, 28, 53, 0.3);
  }
}
.main-menu__wrapper {
  width: 270px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 26px;
  margin-bottom: 71px;
}
@media (min-width: 768px) {
  .main-menu__wrapper {
    width: 688px;
    justify-content: flex-start;
    padding-top: 0;
    margin-bottom: 91px;
  }
}
.main-menu__logo {
  height: 15px;
}
@media (min-width: 768px) {
  .main-menu__logo {
    width: 166px;
    height: 42px;
    margin-right: 167px;
    padding-top: 5px;
    text-align: right;
    background-repeat: no-repeat;
  }
}
@media (min-width: 1440px) {
  .main-menu__logo {
    width: 200px;
    height: 50px;
    background-image: url("../img/bg-logo.svg");
    background-repeat: no-repeat;
  }
}
@media (min-width: 768px) {
  .main-menu__image {
    width: 166px;
    height: 42px;
  }
}
.main-menu__toggle {
  display: block;
  width: 18px;
  height: 18px;
  padding: 0;
  border: none;
  background-color: initial;
  background-image: url("../img/icon-close-menu-mobile.svg");
  background-repeat: no-repeat;
  cursor: pointer;
  margin-right: 3px;
}
.main-menu__toggle:hover {
  opacity: 0.6;
}
.main-menu__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-menu__toggle {
    width: 22px;
    height: 22px;
    background-image: url("../img/icon-close-menu-tablet.svg");
    margin-top: 15px;
    margin-right: 145px;
  }
}
.main-menu__navigation,
.main-menu__user {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
}
.main-menu__user {
  width: 270px;
  margin: 0 auto;
  margin-bottom: 41px;
}
@media (min-width: 768px) {
  .main-menu__user {
    width: 688px;
    box-sizing: border-box;
    padding-left: 57px;
    margin-bottom: 129px;
  }
}
.main-menu__navigation {
  margin-bottom: 54px;
  text-align: center;
}
@media (min-width: 768px) {
  .main-menu__navigation {
    width: 688px;
    margin: 0 auto;
    padding-left: 57px;
    box-sizing: border-box;
    margin-bottom: 132px;
  }
}
.main-menu__user-item:first-child {
  margin-bottom: 51px;
}
@media (min-width: 768px) {
  .main-menu__user-item:first-child {
    display: none;
  }
}
.main-menu__link {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-family: inherit;
  font-size: 30px;
  line-height: 35px;
  font-weight: 500;
  color: #192144;
  cursor: pointer;
}
.main-menu__link:hover {
  color: #FFD74B;
}
.main-menu__link:active {
  color: rgba(255, 215, 75, 0.3);
}
@media (min-width: 768px) {
  .main-menu__link {
    text-align: left;
    font-size: 60px;
    line-height: 71px;
    font-weight: 500;
  }
}
.main-menu__link--login {
  font-size: 17px;
  line-height: 30px;
  font-weight: 700;
  background-color: #FFD74B;
  border-radius: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 78px;
  padding-right: 81px;
}
.main-menu__link--login:hover {
  color: #192144;
  background-color: #ffbd30;
}
.main-menu__link--login:active {
  background-color: #ffbd30;
  color: rgba(22, 28, 53, 0.3);
}
.main-menu__link--mail,
.main-menu__link--phone {
  position: relative;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
}
.main-menu__link--mail:hover,
.main-menu__link--phone:hover {
  color: #FF8D30;
}
.main-menu__link--mail:active,
.main-menu__link--phone:active {
  color: rgba(255, 215, 75, 0.3);
}
@media (min-width: 768px) {
  .main-menu__link--mail,
  .main-menu__link--phone {
    font-size: 28px;
    line-height: 28px;
  }
}
.main-menu__elipse {
  display: inline-block;
  box-sizing: border-box;
  width: 45px;
  height: 45px;
  background-color: #121936;
  border-radius: 50%;
  padding-top: 14px;
  padding-left: 13px;
  margin-right: 11px;
  vertical-align: middle;
}
@media (min-width: 768px) {
  .main-menu__elipse {
    width: 68px;
    height: 68px;
    padding-left: 20px;
    padding-top: 20px;
    margin-right: 14px;
    vertical-align: baseline;
  }
}
.main-menu__elipse--mail {
  padding-left: 14px;
  padding-top: 16px;
}
@media (min-width: 768px) {
  .main-menu__elipse--mail {
    padding-left: 21px;
    padding-top: 22px;
  }
}
@media (min-width: 768px) {
  .main-menu__elipse svg {
    width: 27.2px;
    height: 25.69px;
  }
}
@media (min-width: 768px) {
  .main-menu__elipse--mail svg {
    width: 27.62px;
    height: 18.89px;
  }
}
.main-menu__link--phone svg,
.main-menu__link--mail svg {
  fill: #ffd74b;
}
.main-menu__link--phone:hover .main-menu__elipse svg,
.main-menu__link--mail:hover .main-menu__elipse svg {
  fill: #ffbd30;
}
.main-menu__link--phone:active .main-menu__elipse svg,
.main-menu__link--mail:active .main-menu__elipse svg {
  fill: #ffbd30;
  opacity: 0.3;
}
.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: none;
}
@media (min-width: 1440px) {
  .site-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 500px;
  }
}
.site-list__link {
  display: block;
  text-decoration: none;
  font-family: inherit;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 21px;
}
@media (min-width: 1440px) {
  .site-list__link {
    margin-top: 19px;
    margin-right: 34px;
    cursor: pointer;
  }
  .site-list__link:hover {
    color: #FFD74B;
  }
  .site-list__link:active {
    color: rgba(255, 215, 75, 0.3);
  }
}
.site-list__link--scroll-menu {
  color: #192144;
}
.user-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: row;
}
@media (min-width: 1440px) {
  .user-list {
    margin-top: 3px;
  }
}
.user-list__link {
  display: none;
  text-decoration: none;
  font-family: inherit;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: #161C35;
  text-transform: uppercase;
}
@media (min-width: 1440px) {
  .user-list__link {
    display: block;
  }
}
.user-list__link--phone,
.user-list__link--mail {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  box-sizing: border-box;
  padding-top: 15px;
}
.user-list__link--phone {
  padding-left: 15px;
  background-color: #FF8D30;
}
.user-list__link--phone:hover {
  background-color: #f26926;
}
.user-list__link--phone:active {
  background-color: #f26926;
}
.user-list__link--mail {
  padding-left: 17px;
  background-color: #ffbd30;
}
.user-list__link--mail:hover {
  background-color: #FF8D30;
}
.user-list__link--mail:active {
  background-color: #FF8D30;
}
.user-list__link--login {
  background-color: #FFD74B;
  padding-top: 14px;
  padding-bottom: 16px;
  padding-left: 29px;
  padding-right: 28px;
  border-radius: 25px;
  cursor: pointer;
  max-width: 188px;
}
.user-list__link--login:hover {
  background-color: #ffbd30;
}
.user-list__link--login:active {
  color: rgba(22, 28, 53, 0.3);
}
@media (min-width: 768px) {
  .user-list__link--login {
    display: block;
    padding-left: 30px;
  }
}
@media (min-width: 1440px) {
  .user-list__link--login {
    padding-top: 14px;
    padding-bottom: 16px;
  }
}
.user-list__link--phone:active svg,
.user-list__link--mail:active svg {
  opacity: 0.3;
}
.user-list__link--phone svg {
  width: 18px;
  height: 18px;
}
.user-list__link--mail svg {
  width: 16px;
  height: 14px;
}
.page-main {
  position: relative;
  display: flex;
  flex-direction: column;
}
.intro {
  position: relative;
  background-color: #161C35;
  background-image: url("../img/bg-main-mobile.jpg");
  background-repeat: no-repeat;
  background-position: 0 -60px;
  padding-top: 31px;
  padding-bottom: 0;
  z-index: 7;
}
@media (min-resolution: 144dpi), (min-resolution: 2dppx) {
  .intro {
    background-image: url("../img/bg-main-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
.intro::before {
  content: "";
  position: absolute;
  right: 15%;
  top: 162px;
  width: 192px;
  height: 255px;
  background-image: url("../img/traveller-mobile.png");
  background-repeat: no-repeat;
  z-index: 2;
}
@media (min-resolution: 144dpi), (min-resolution: 2dppx) {
  .intro::before {
    background-image: url("../img/traveller-mobile@2x.png");
    background-size: 192px auto;
  }
}
.intro::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 285px;
  background-color: #161C35;
  left: 0;
  bottom: 267px;
  z-index: -1;
}
@media (min-width: 768px) {
  .intro::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .intro {
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 29px;
    padding-bottom: 96px;
    background-color: #182044;
    background-image: url("../img/bg-main-tablet.jpg");
    background-position: 0 252px;
  }
  .intro::before {
    width: 372px;
    height: 451px;
    background-image: url("../img/traveller-tablet.png");
