.state-list {
  margin: 0;
  padding: 0;
  grid-column: 1/3;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding-left: 7px;
  padding-right: 20px;
  margin-bottom: 6px;

  @media (min-width: @tablet-width) {
    position: relative;
    flex-direction: row;
    margin-bottom: 0;
    padding-left: 2px;
    padding-right: 2px;
    grid-column: 1/4;
  }

  @media (min-width: @desktop-width) {
    grid-column: 3;
    grid-row: 1/3;
    padding-top: 47px;
    padding-left: 15px;
    flex-direction: column;
  }

  &--mod {
    margin-bottom: 11px;

    @media (min-width: @tablet-width) {
      margin-bottom: 0;
    }
  }
}

.state-list__item {
  display: flex;
  flex-direction: row;
  margin-bottom: 4px;

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
    margin-right: 29px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 12px;
    margin-right: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.state-list__item picture {
  @media (min-width: @desktop-width) {
    display: block;
    width: 35px;
    height: 24px;
    margin-right: 18px;
  }
}

.state-list__img {
  margin-right: 14px;

  @media (min-width: @tablet-width) {
    margin-right: 10px;
    margin-top: 2px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 18px;
    display: block;
  }
}

.state-list__name {
  padding-top: 3px;
  font-size: 15px;
  line-height: 16px;
  font-weight: 500;
  color: @basic-blue-light;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 20px;
    padding-top: 5px;
  }
}
